//
// social-list
// --------------------------------------------------
.social-list {
    display: inline-block;
    list-style: none;
    margin: 0 0 30px 0;
    padding: 0;
    font-size: 0;
}

.social-list > * {
    display: inline-flex;
    align-items: center;
    margin: 0 10px 0 0;
}

.social-list-large > * { margin: 0 20px 0 0; }
.social-list > *:last-child { margin: 0; }

.social { 
    background-color: @gray-lighter;
    transition: @transition-base;
    width: 32px;
    height: 32px;
    text-align: center;
    border-radius: 16px;
}

.social-large {
    background-color: @light;
    width: 50px;
    height: 50px;
    border-radius: 25px;
}

.social-facebook:hover { background-color: @color-facebook; }
.social-github:hover { background-color: @color-github; }
.social-linkedin:hover { background-color: @color-linkedin; }
.social-twitter:hover { background-color: @color-twitter; }