//
// Tier
// --------------------------------------------------

.tier {
	position: relative;
	padding: @tier-padding;
	overflow: hidden;
	@media (max-width: @grid-float-breakpoint) {

		padding: @tier-padding-mobile;

		.container {
			padding-left: 0;
			padding-right: 0;
			> div {
				padding-left: 0;
				padding-right: 0;
			}
		}

		&.tier-bg-gray {
			.container {
				padding-left: 0;
				padding-right: 0;
				> div {
					padding-left: 20px;
					padding-right: 20px;
				}
			}
			padding-left: 35px;
			padding-right: 35px;		
		}
	}
}

.tier1 {
	@media (max-width: @grid-float-breakpoint) {
		padding: 44px 20px 20px;
	}	
}

.tier {
	display: flex;
	align-items: center;
}

.tier-img {
	position: absolute;
	bottom: 0;
	left: 0;
	min-width: 100%;
	min-height: 100%;
	max-width: none;
	z-index: -1;
}


.tier-right .tier-img {
	position: absolute;
	bottom: 0;
	right: 0;
	left: auto;
	min-width: 100%;
	min-height: 100%;
	z-index: -1;
}

.tier-img-lg { display: none; }

.tier-white { color: #fff; }

.tier-bg-gray {
	background: @brand-primary;
	padding: @tier-padding-gray !important;
}

@media screen and (min-width: 768px) {
	.tier {
		padding: @tier-padding-large;
	}

	.tier-img-sm { display: none; }
	.tier-img-lg { display: block; }
}