//
// Categories
// --------------------------------------------------

.categories {
    list-style: none;
    //margin: 0 0 -10px -10px;
    padding: 0;
    font-size: 0;
    text-align: center;
}

.categories > a {
    text-decoration: none;
}


.categories-left { text-align: left; }

.category {
    display: inline-block;
    margin: 0 0 10px 10px;
    padding: @padding-base-vertical @padding-base-horizontal;
    border: 1px solid @gray-dark;
    border-radius: 17px;
    color: @gray-dark;
    font-size: @font-size-small;
    line-height: 1;
    cursor: pointer;
    transition: @transition-base;
}

.contributors .category,
.contributors .category:before,
.contributors .category:after,
.contributors .category * {
    box-sizing: initial !important;
}

.category:hover {
    border-color: @gray-light;
    color: @gray-light;
}

.categories-white .category { 
    color: @light;
    border-color: @light;
}

.category-0 { background: rgba(0,34,102,0); }
.category-0:hover,
.category-0.active {
    border-color: rgba(0,34,102,1);  
      background: rgba(0,34,102,1); 

    color: @light; 
}

.tier-bg-gray .category-0:hover,
.tier-bg-gray .category-0.active {
    border-color: @gray-lighter;  
    background: rgba(0,34,102,1); 

    color: @gray-lighter; 
}

.category-1 { background: rgba(0,160,0,0); }
.category-1:hover,
.category-1.active { 
    border-color: rgba(0,160,0,1);
      background: rgba(0,160,0,1); 

    color: @light; 
}

.category-2 { background: rgba(170,17,51,0); }
.category-2:hover,
.category-2.active { 
    border-color: rgba(170,17,51,1);
      background: rgba(170,17,51,1); 

    color: @light; 
}

.category-3 { background: rgba(47,83,156,0); }
.category-3:hover,
.category-3.active { 
    border-color: rgba(47,83,156,1); 
      background: rgba(47,83,156,1); 

    color: @light; 
}