//
// post-list
// --------------------------------------------------

.post-list {
    margin: 50px 0 0 0;
    padding: 0;
    list-style: none;
    color: @gray-dark;
    font-size: 0;

    li {
        vertical-align: top;
    }
}

.post-list > * {
    margin: 0 0 100px 0;
    font-size: @font-size-base;
}

.post-list li {

}

.post-list-small {
    margin: 25px 0 -50px -25px;
}

.post-list-small > * {
    display: inline-block;
    width: 100%;
    padding: 0 0 51px 25px;
    margin: 0;
}

@media screen and (min-width: 480px) {
    .post-list-small > * {
        width: 50%;
    }
}


//
// post-meta
// --------------------------------------------------

.post-meta {
    font-size: @font-size-large;
    font-style: italic;
    text-transform: uppercase;
}

.post-meta-small {
    font-size: @font-size-base;
}

//
// post-link
// --------------------------------------------------

.post-link {
    color: @gray-dark;
}

.post-link-light {
    color: @light;
}

//
// post-title
// --------------------------------------------------

.post-title {
    color: @gray-dark;
}

.post-title-light {
    color: @light;
}