//
// logo
// --------------------------------------------------

.logo {
	width: 144px;
	height: 60px;
	transition: @transition-base;
	
	@media (max-width: @grid-float-breakpoint) {
		width: 72px;
		height: 30px;
	}
}

.logo-gt {
	fill: @brand-orange;
}

.logo-hpd,
.logo-acc {
	fill: rgba(255,255,255,1);
	transition: fill @transition-base;
}

.logo:hover {
	opacity: 0.7;
}

.navbarLow .logo-hpd,
.navbarLow .logo-acc {
	fill: rgba(0,0,0,1);
}
