//
// hdg
// --------------------------------------------------

.hdg { 
    font-family: @font-family-brand; 
    font-weight: normal;
    margin: 0.25em 0 0.5em 0; 
    line-height: 1.3;
}

.hdg_hero { 
    font-size: 36px; // ~40
    text-transform: uppercase;
}

.hdg_1 { font-size: ceil((@font-size-h1 * 0.67)); } // ~26
.hdg_2 { font-size: ceil((@font-size-h2 * 0.67)); } // ~20
.hdg_3 { font-size: ceil((@font-size-h3 * 0.67)); } // ~16
.hdg_4 { font-size: ceil((@font-size-h4 * 0.67)); } // ~15
.hdg_5 { font-size: ceil((@font-size-h5 * 0.67)); } // ~12
.hdg_6 { font-size: ceil((@font-size-h6 * 0.67)); } // ~8

.hdg_spaced { margin: 125px 0; }

@media screen and (min-width: 500px) {
    .hdg_1 { font-size: @font-size-h1; }
    .hdg_2 { font-size: @font-size-h2; }
    .hdg_3 { font-size: @font-size-h3; }
    .hdg_4 { font-size: @font-size-h4; }
    .hdg_5 { font-size: @font-size-h5; }
    .hdg_6 { font-size: @font-size-h6; }
}

@media screen and (min-width: 768px) {
    .hdg_hero { font-size: @font-size-hero; }
}

@media screen and (max-width: 768px) {
    .hdg_1 { font-size: 24px; }
    .hdg_2 { font-size: @font-size-h2; }
    .hdg_3 { font-size: @font-size-h3; }
    .hdg_4 { font-size: @font-size-h4; }
    .hdg_5 { font-size: @font-size-h5; }
    .hdg_6 { font-size: @font-size-h6; }
}