//
// post-author
// --------------------------------------------------

.post-author:after {
	content: " ";
	display: table;
	clear: both;
}

.headshot { 
	float: left;
	width: 80px;
	height: 80px;
	margin: 0;
	border-radius: 40px 0 40px 40px;
	overflow: hidden;
	background: @brand-primary;
}
.headshot-light {
	background: @gray-light;
}

.headshot > * {
	width: 100%;
}

@media screen and (min-width: 768px) {
	.headshot-large {
		width: 160px;
		height: 160px;
		border-radius: 80px 0 80px 80px;
	}
}