//
// icn
// --------------------------------------------------
.icn {
    display: inline-block;
    margin: 0 auto;
    text-indent: -9999px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: cover;
}
.icn_arrowRight { 
    width: 32px; 
    height: 32px; 
    background-image: url(../img/icon/icn_arrowRight-black.svg);
}
.icn_arrowLeft { 
    width: 32px; 
    height: 32px; 
    background-image: url(../img/icon/icn_arrowLeft-black.svg);
}
.icn_facebook { 
    width: 8px; 
    height: 16px; 
    background-image: url(../img/icon/icn_facebook.svg);
}
.icn_facebook-footer {
    width: 15px;
    height: 28px;
    background-image: url(../img/icon/icn_facebook-gray.svg);
}
.icn_github-footer {
    width: 25px;
    height: 24px;
    background-image: url(../img/icon/icn_github-gray.svg);
}
.icn_linkedin { 
    width: 16px; 
    height: 16px; 
    background-image: url(../img/icon/icn_linkedin.svg);
}
.icn_twitter { 
    width: 18px; 
    height: 15px; 
    background-image: url(../img/icon/icn_twitter.svg);
}
.icn_twitter-footer {
    width: 24px;
    height: 21px;
    background-image: url(../img/icon/icn_twitter-gray.svg);
}